<template>
    <section class="main-content">
        <div class="accordion md-accordion left-filter mt-2" id="accordionEx" role="tablist" aria-multiselectable="true">
            <div class="card">
                <div class="card-header filter-head" role="tab" id="headingOne1">
                    <h5 class="mb-0">
                        <em class="fa fa-file font-16"></em>ADDITIONAL STORAGE
                        <span class="expand_all_filters">
                            <span>
                                <a id="AdditionalStorage" @click="OpenAdditionalStoragePopUp" href="javascript:;" data-toggle-tooltip="tooltip" data-original-title="Add Additional Storage">
                                    <i class="fa fa-plus text-white" style="font-size: 18px; margin:13px"></i>
                                </a>
                            </span>
                        </span>
                    </h5>
                </div>
                
            </div>
        </div>
        <div id="main" class="collapse show border FullTreeData" style="max-height:90vh; overflow-y: auto;">
            <div id="accordion" v-for="(item,index) in AdditionalStorage" :key="index">
                <div class="d-flex"  >
                    <div class="float-left p-0 col-3 text-center pt-2"><img src="https://talygenv.github.io/Vue-UI-Content/images/driveicon_1.png" height="40" /></div>
                    <div class="float-right pl-0 col-9">
                        <h5 class="mb-0">
                            <a class="btn btn-link w-100 text-left p-0 text-decoration-none" @click="GetOrganizationSpace(item.Id,index)" :id="item.Id" data-toggle="collapse" data-target="#collapseOne" :aria-expanded="isOpenAccordion === index" aria-controls="collapseOne"
                            data-parent="#accordion">
                                {{item.Name}}  
                            </a>
                        </h5>
                        <b-progress style="height: 0.8rem;" :value="UsedStorage" :max="TotalStorage" class=""></b-progress>
                        <span class="font-12 font-weight-medium text-secondary">{{ UsedStorage }} GB of {{ TotalStorage }} GB Used</span>
                    </div>
                </div>

                <div :id="'collapse'+index" class="collapse" :class="{ 'show': isOpenAccordion === index }" aria-labelledby="headingOne" data-parent="#accordion" :key="index">
                    <div class="card-body">
                    <DocCommonTreeView :organizationSpace="additionalOrganizationSpace"
                            :RecentFolders="RecentPinnedFolderArray"
                            mySpaceOrOrganization="1"
                            :pinnedFolders="pinnedFolderArray"
                            :MySpaceFolders="FolderArray"
                            :sharedFolders="sharedChildFolder"
                            :routeClass="routeClass"
                            :refCode="refCode"
                            :addClsLeftMenu="addClsLeftMenu"
                            :AdditionalDriveId="AdditionalDriveId"
                            type="Additional"
                            :treeId="'Additional'+index"
                            @Folderlisting="Folderlisting"
                            @SharedFolderIdMoveLeftMenu="SharedFolderIdMoveLeftMenu"
                            :SharedFolderIdMoveLeftMenu="SharedFolderIdMoveLeftMenu"
                            @FolderIdMoveLeftMenu="FolderIdMoveLeftMenu"
                            :checkActionType="checkActionType" :userid="userid" :key="RefreshDocCommon">
                    </DocCommonTreeView>
                    </div>
                </div>
            </div>
        </div>
        <AddStorageAccount v-if="openStoragePopup" @ClosePopup="ClosePopup"></AddStorageAccount>
    </section>
</template>
<script>
import DataService from '../../services/DataService';
import AddStorageAccount from '../../views/UI/Modal/AddStorageAccount.vue';
import DocCommonTreeView from '../DocCommonTreeView.vue';
export default{
    components:{
        DocCommonTreeView,
        AddStorageAccount
    },
    created(){
        var vm=this;
        vm.GetAdditionalStorage();
    },
    props: {
        MySpaceFolders: {
            type: Array,
            required: true
        },
        SharedFolder: {
            type: Array,
            required: true
        },
        userid: {
            type: Number,
            required: true
        },
        sharedChildFolder: {
            type: Array,
            required: true
        },
        RecentPinnedFolderArray: {
            type: Array,
            required: true
        },
        pinnedFolderArray: {
            type: Array,
            required: true
        },
        organizationSpace:{
            type: Array,
            required: true 
        },
        addClsLeftMenu:{
            type: Boolean,
            required: false 
        },
        FolderArray:{
            type: Array,
            required: true 
        },
        folderTreeList:{
            type: Array,
            required: true 
        },
        // FolderIdMoveLeftMenu:{
        //     type: Function,
        //     required: true
        // },
        // SharedFolderIdMoveLeftMenu:{
        //     type: Function,
        //     required: true
        // },
        // OrganizationFolderIdMoveLeftMenu:{
        //     type: Function,
        //     required: true
        // },
        // FindalFolderArray:{
        //     type: Array,
        //     required: true
        // },
        checkActionType:{
            type: String,
            required: false
        },
        routeClass:null,
        refCode:{
            type: String,
            required: false
        },
        
    },
    data(){
        return{
            TotalStorage:0,
            UsedStorage:0,
            AdditionalStorage:[],
            RefreshDocCommon:0,
            openStoragePopup:false,
            isOpenAccordion:null,
            additionalOrganizationSpace:[],
            addClsLeftMenu:false
        }
    },
    methods:{
        ClosePopup:function(){
            var vm=this;
            vm.openStoragePopup=false;
        },
        OpenAdditionalStoragePopUp:function(){
            var vm=this;
            vm.openStoragePopup=true;
        },
        AdditionalStorageId:function(id){
            var vm=this;
            vm.RefreshDocCommon+=1;
        },
        GetOneDriveStorage:async function(){
            var vm=this;
            vm.AdditionalStorage.forEach((itm,index)=>{
                var params=`additionalStorageId=${itm.Id}`
            DataService.GetOneDriveStorage(params).then((response) => {
               //debugger
                if(response.data!=""){
                    vm.TotalStorage=(response.data.quota.total/1073741824).toFixed(2);
                    vm.UsedStorage=(response.data.quota.used/1073741824).toFixed(2);
                }
            })
        })
        },
        FolderIdMoveLeftMenu: function (data, parentid, actionVal, name,routeListingcode,additionalStorageFolderId,driveid) {
            this.$emit('FolderIdMoveLeftMenu',data, parentid, actionVal, name,routeListingcode,additionalStorageFolderId,driveid);
        },
        Folderlisting: function (data, parentid, actionVal, name,routeListingcode,driveId) {
            //debugger
            var selectedfolder=$('.selectedfolder').attr('id');
            try {
                if(routeListingcode!=selectedfolder && selectedfolder!=undefined){
                this.$parent.$parent.$refs.listing.searchCondition ="";
                this.$parent.$parent.$refs.listing.listType='ListView';

            }
            } catch (error) {
                console.log(error)
            }
           
            this.$emit('Folderlisting',data, parentid, actionVal, name,routeListingcode,driveId);
        },
        StorageDocumentListing:function(folderiddata, Parentid, actionVal, name, searchcondition, sortBy, sortExp, listingCode, sharedPermission,pageno,clikType,addFolderId,driveId){
            //debugger
            var vm=this;
            vm.$emit('StorageDocumentListing',folderiddata, Parentid, actionVal, name, searchcondition, sortBy, sortExp, listingCode, sharedPermission,pageno,clikType,addFolderId,driveId)
        },
        GetOrganizationSpace: function (id,indx) {
            var vm = this;
            vm.AdditionalDriveId=id;
            vm.additionalOrganizationSpace=[];
            if (this.isOpenAccordion === indx) {
                this.isOpenAccordion = null; // Close the accordion if it's already open
            } else {
                this.isOpenAccordion = indx; // Open the clicked accordion
            }
            DataService.GetOrganizationFolderTreeData().then((response) => {
                response.data.folderTreeList.forEach((value, index) => {
                    var CheckIfExist = vm.organizationSpace.filter(function (el) {
                        return el.FOLDER_ID == value.FOLDER_ID;
                    });
                    //if (CheckIfExist.length == 0) {
                        if(parseInt(value.additional_storage_id)==parseInt(id)){
                            if (value.FOLDER_PARENT_ID == null) {
                                let folderId = value.FOLDER_ID
                                let rslt = vm.getsharedChildFolder(folderId, value, vm.folderTreeList);
                                vm.additionalOrganizationSpace.push(rslt)
                                vm.RefreshDocCommon++;
                            }
                        }
                    //}
                });
            });
            vm.RefreshDocCommon+=1;
            this.AdditionalStorageId();
        },
        getsharedChildFolder: function (folderId, currentItem, data) {
            
            let ths = this;
            let retrunArray = currentItem
            var childArray = data.filter(function (el) {
                return el.FOLDER_PARENT_ID == folderId;
            });
            let finalChildArray = [];
            childArray.forEach((value, index) => {
                let ChildfolderId = value.FOLDER_ID
                ths.str+=ChildfolderId +',';
                var CheckIfExist1 = finalChildArray.filter(function (el) {
                    return el.FOLDER_ID == value.FOLDER_PARENT_ID;
                });
                if (CheckIfExist1.length == 0) {
                    var CheckIfExist = data.filter(function (el) {

                        return el.FOLDER_PARENT_ID == ChildfolderId;


                    });
                    if (CheckIfExist != null && CheckIfExist.length > 0) {
                        let rslt = ths.getsharedChildFolder(ChildfolderId, value, data);
                        finalChildArray.push(rslt);
                    } else {
                        finalChildArray.push(value);
                    }
                }
            });
            retrunArray.chidFolder = finalChildArray;
            return retrunArray
        },
        GetAdditionalStorage:function(){
            var vm=this;
            DataService.GetAdditionalStorage().then((response) => {
                vm.AdditionalStorage=JSON.parse(response.data);
            })
        }
    }
}
</script>

